<template>
  <div class="change-password">
    <van-nav-bar
      title="修改密码"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="password"
        type="password"
        placeholder="请输入"
        label="原密码"
      />
      <van-field
        v-model="password1"
        type="password"
        placeholder="请输入"
        label="新密码"
      />
      <van-field
        v-model="password2"
        type="password"
        placeholder="请输入"
        label="新密码确认"
      />
    </van-cell-group>
    <div class="btn-wrap">
      <van-button type="info" @click="submit" block>提交</van-button>
    </div>
  </div>
</template>

<script>
import { resetPwd } from '@/api/index';
import { mapState } from 'vuex';

export default {
  data() {
    return {
      password: '',
      password1: '',
      password2: ''
    };
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    submit() {
      if (!this.password) {
        this.$Toast('请输入老密码');
        return;
      }

      if (!this.password1) {
        this.$Toast('请输入新密码');
        return;
      }

      if (this.password1 !== this.password2) {
        this.$Toast('新密码输入不一致');
        return;
      }

      resetPwd({
        oldPassword: this.password,
        newPassword: this.password1,
        userId: this.userInfo.userId
      }).then(res => {
        if (res) {
          if (res.data.code === 0) {
            this.$Toast('密码修改成功');
            this.$router.go(-1);
          } else {
            this.$Toast(res.data.msg);
          }
        } else {
          this.$Toast('密码修改失败');
        }
      });
    }
  }
};
</script>

